import React from 'react'
import { AiOutlinePhone, AiOutlineMail } from 'react-icons/ai'
import {FiFacebook} from 'react-icons/fi'
const Ecard = () => {
  return (
    <>
      <br />
      <br />
      <br />
      <br />
      <div
        class='card'
        style={{
          boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2)',
          transition: '0.3s',
          width: '30vh',
          margin: '0 auto',
          backgroundColor: '#FFD400',
        }}
      >
        <br />

        <img
          src='https://thumbor.forbes.com/thumbor/711x572/https://specials-images.forbesimg.com/dam/imageserve/1080529720/960x0.jpg?fit=scale'
          alt='Avatar'
          style={{
            width: '200px',
            height: '200px',
            borderRadius: '50%',
            margin: '0 auto',
          }}
        />
        <br />
        <div
          class='container'
          style={{
            padding: '4%',
            fontSize: '1rem',
            margin: '0 auto',
            backgroundColor: '#fff',
          }}
        >
          <h3 style={{ align: 'center', textAlign: 'center' }}>John Doe</h3>
          <h5 style={{ align: 'center', textAlign: 'center' }}>
            {' '}
            Architect and Engineer
            <hr />
          </h5>
          <h5>
            {' '}
            <AiOutlineMail></AiOutlineMail>&nbsp; &nbsp;&nbsp;johndoe@gmail.com
          </h5>
          <h5>
            <AiOutlinePhone></AiOutlinePhone> &nbsp; &nbsp;9765402942
          </h5>

          <tr>
            <td>
              {' '}
              <b></b> <br />
            </td>
            <td>&nbsp; &nbsp; &nbsp;</td>
            <td>&nbsp; &nbsp; &nbsp; </td>
            <td>&nbsp; &nbsp; &nbsp; </td>
            <td>&nbsp; &nbsp; &nbsp; </td>
          </tr>
          <tr>
            <td>
              {' '}
              <br />
            </td>
          </tr>
        </div>
      </div>
    </>
  )
}

export default Ecard